import { Controller, Selection } from '@ali/4ever-cangjie';

function filterSameHeading(headings: string[]) {
  const keyMap = {};
  return headings.filter(heading => {
    if (keyMap[heading]) {
      return false;
    }
    keyMap[heading] = true;
    return true;
  });
}


export default function getHeadingAtRanges(controller: Controller, ranges: Selection[]) {
  const { document } = controller.value;
  let headings: string[] = [];
  ranges.forEach(range => {
    headings = headings.concat(document.getLeafBlocksAtRange(range).map(block => block.type));
  });
  return filterSameHeading(headings);
}
